import React from 'react'
import Loadable from '@loadable/component'
const OwlCarousel = Loadable(() => import('react-owl-carousel3'))
const options = {
    loop: true,
    navContainerClass:"carousel-btn-block banner-carousel-btn",
    nav: true,
    dots: false,
    animateIn:"fadeIn",
    animateOut:'slideOutDown',
    autoplayHoverPause: false,
    autoplay: false,
    margin: 30,
    smartSpeed: 1200,
    // navElements:'<div class="carousel-btn-block banner-carousel-btn"></div>',
    navText: [
      	'<span class="carousel-btn left-btn"><i class="flaticon-left-arrow-1 left"></i></span>',
      	'<span class="carousel-btn right-btn"><i class="flaticon-right-arrow right"></i></span>'
    ],
    responsive: {
        0: {
            items: 1,
        },
        480:{
            items:1,
        },
        600: {
            items: 1,
        },
        768: {
            items: 1
        },
        992: {
            items: 1
        },
        1200: {
            items: 1
        },
        1280: {
            items: 1
        }
    }
}

const HomeSlider4=({bgimg})=> {
    return (
            <div className="main-banner-wrapper">
                <OwlCarousel className="banner-style-one owl-theme owl-carousel" {...options}>
                    <div className="slide slide-one" id={"slide-one"+bgimg}></div>
                    <div className="slide slide-one" id={"slide-two"+bgimg}></div>
                    <div className="slide slide-one" id={"slide-three"+bgimg}></div>
                </OwlCarousel>
                {/* <div className="carousel-btn-block banner-carousel-btn">
                <span className="carousel-btn left-btn"><i className="flaticon-left-arrow-1 left"></i></span>
                <span className="carousel-btn right-btn"><i className="flaticon-right-arrow right"></i></span>
                </div>  */}
            </div>         
    )
}

export default HomeSlider4
