import React,{useState} from 'react'
import {Link} from 'gatsby'
import { Row,Col,Nav,Tab } from 'react-bootstrap'
import AdvanceSearchOption from '../../../../../Global/App/AdvanceSearchOption/AdvanceSearchOption.component'
import BreadcrumContent from '../../../../../Global/Pages/BreadcrumbContent.component'
import CategoriesProperty from '../../../../../Global/Pages/CategoriesProperty.component'
import SidebarFeatured from '../../../../../Global/Pages/SideBarFeaturedListing.component'
import ListGridView from '../../../../../Global/Pages/Listing/ListGridView.component'
import SearchResult from '../../../../../Global/Pages/Listing/SearchResult.component'
import MortageCalculator from '../../../../../Global/Pages/Listing/Style/MortageCalculator.component'
import AdvanceSearchWidgetSidebar from '../../../../../Global/App/SidebarWidgets/AdvancedSearchWidget.component'
import HomeSlider4 from '../../../../../Global/App/Slider/HomeSlider4.component'
const SliderSection=() =>{
    const [SideBarShow, setSideBarShow] = useState(false);
    const handleSidebar=()=>{
        setSideBarShow(!SideBarShow)
            document.querySelector('body').classList.toggle('body_overlay')
            document.querySelector('.sidebar_content_details').classList.toggle('sidebar_ml0')
    }
    const HomeIconbox=()=>{
        return(
            <div className="container home_iconbox_container">
			<div className="row posr">
				<div className="col-lg-12">
					<div className="home_content listing slider_style">
						<div className="home-text home6 text-center">
							<h2 className="fz50 color-white">Welcome, Make Yourself at Home</h2>
							<p className="fz18 color-white">Parallax banner with background image.</p>
						</div>
                        <div className="home_adv_srch_opt listing">
                        <Tab.Container id="left-tabs-example" defaultActiveKey="buy">
                                    <Row>
                                        <Col sm={12}>
                                            <Nav id="pills-tab" className="nav nav-pills">
                                                <Nav.Item>
                                                    <Nav.Link eventKey="buy">Buy</Nav.Link>
                                                </Nav.Item>
                                                <Nav.Item>
                                                    <Nav.Link eventKey="rent">Rent</Nav.Link>
                                                </Nav.Item>
                                            </Nav>
                                        </Col>
                                        <Col sm={12}>
                                            <Tab.Content id="pills-tabContent" className="home1_adsrchfrm">
                                                <Tab.Pane eventKey="buy">
                                                    <AdvanceSearchOption ClassName="home6" index="1" style2/>
                                                </Tab.Pane>
                                                <Tab.Pane eventKey="rent">
                                                <AdvanceSearchOption  index="2"/>
                                                </Tab.Pane>
                                            </Tab.Content>
                                        </Col>
                                    </Row>
                                </Tab.Container>
                            </div>
                        </div>
				</div>
			</div>
		</div>
        )
    }
    return (
        <div>
        <div className="home-listing-slider">
            <div className="container-fluid p0">
                <div className="row">
                    <div className="col-lg-12">
                    <HomeSlider4 bgimg="sliderstyle"/>
                    </div>
                </div>
            </div>
            <HomeIconbox/>
        </div>
        <section className="our-listing bgc-f7 pb30-991" style={{marginTop:'60px'}}>
			<div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <div className="listing_sidebar dn db-991">
                            <div className="sidebar_content_details style3">
                                <div className="sidebar_listing_list style2 mobile_sytle_sidebar mb0">
                                    <div className="sidebar_advanced_search_widget">
                                        <h4 className="mb25">
                                    Advanced Search <Link className="filter_closed_btn float-right" to="#" onClick={handleSidebar}><small>Hide Filter</small> <span className="flaticon-close"></span>
                                    </Link>
                                    </h4>
									<AdvanceSearchWidgetSidebar style2/>
								    </div>
                                </div>
                            </div>
                        </div>
                    </div>
			    </div>
               
                <div className="row">
                    <div className="col-lg-6">
                    <BreadcrumContent title="Simple Listing – List View" active="Simple Listing – List View"/>
                    </div>
                
                 <div className="col-lg-6">
                    <div className="listing_list_style mb20-xsd tal-991">
                        <ul className="mb0">
                            <li className="list-inline-item"><a href="#"><span className="fa fa-th-large"></span></a></li>
                            <li className="list-inline-item"><a href="#"><span className="fa fa-th-list"></span></a></li>
                        </ul>
                    </div>
                    <div className="dn db-991 mt30 mb0">
						<div id="main2">
							<span id="open2" className="flaticon-filter-results-button filter_open_btn style2" onClick={handleSidebar}> Show Filter</span>
						</div>
					</div>
                </div>
                </div>
                <div className="row">
                <div className="col-md-12 col-lg-8">
                        <div className="row">
                         <SearchResult/>
                        </div>
                        <ListGridView/>
                    </div>
                    <div className="col-lg-4 col-xl-4">
                    <div className="sidebar_listing_grid1 dn-991">
						<div className="sidebar_listing_list">
							<div className="sidebar_advanced_search_widget">
                                <MortageCalculator/>
                            </div>
                        </div>
                        <CategoriesProperty/>
                        <SidebarFeatured HeaderTitle="Recently Viewed"/>
                    </div>
                    </div>
                </div>
            </div>
	    </section> 
	    </div>

    )
}

export default SliderSection
